section {
  width:100%;
  height:100%;
  
  /* Flex initialised */
  display:-ms-flexbox;
  display:flex;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 255, 0.5)), to(rgba(255, 255, 0, 0.5)));
  background: -webkit-linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5));
  background: -o-linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5));
  background: linear-gradient(rgba(0, 0, 255, 0.5), rgba(255, 255, 0, 0.5));
  
  /* Does not distribute equal height with default value 'stretch' */
  -ms-flex-align:center;
      align-items:center;
}
.container {
  width:90%;
  height:100%;
  max-width:450px;
 
  margin:0 auto;
  padding:40px;
  -webkit-box-shadow:0px 0px 5px rgba(0,0,0,0.3);
          box-shadow:0px 0px 5px rgba(0,0,0,0.3);
  text-align:center;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  border-radius:20px;
  
  /* Flex initialised */
  display:-ms-flexbox;
  display:flex;
  
  /* Makes children wrap whole width. Used with flex-basis:100% on child-elements. */
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  
  /* Does not distribute equal height with default value 'stretch' */
  -ms-flex-line-pack:center;
      align-content:center;
}

.container:hover {
  -webkit-box-shadow:0px 4px 20px -8px rgba(0,0,0,0.3);
          box-shadow:0px 4px 20px -8px rgba(0,0,0,0.3);
  background:#fff;
  -webkit-transform: scale(1.06);
      -ms-transform: scale(1.06);
          transform: scale(1.06);
  cursor:pointer;
}

.child-elem {
  
  /* Makes children elements have a width of 100%, prevents auto width-sizing from flex */
  -ms-flex-preferred-size:100%;
      flex-basis:100%;
}

/******** OTHER ELEMENTS + ANIMATIONS *********/

.container {
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}


